@if ($headings and length($headings) > 0) {
  @include scale-headings;

  #{$headings} {
    @include typography('heading');

    margin-top: $headings-spacing * 2;
    margin-bottom: $headings-spacing;
    color: $headings-color;

    &:first-child {
      margin-top: 0;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }
}
